
html, body {
  margin:0;
  padding: 0;
  background-color: #efefef;
  font-family: Helvetica Neue,Helvetica,PingFang SC,Hiragino Sans GB,Microsoft YaHei,\\5FAE\8F6F\96C5\9ED1,Arial,sans-serif;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

.list {
	width: 100%;
}
.list-container {
	width: 1000px;
	margin: 0 auto;
}
.table {
	width: 100%;
	border-collapse:collapse;
	border: 1px solid #eee;
	background: white;
}
.table tr {
	height: 60px;
}
.table tr:nth-child(1) {
	background: #f8f8f9;
}
.table tr:nth-child(1) td {
	font-size: 16px;
	font-weight: 400;
}
.table tr:nth-child(1):hover {
	background: #f8f8f9;
}
.table tr:hover {
	background-color: #ebf7ff;
}
.table tr td:nth-child(1) {
	text-align: center;
}
.table tr td {
	font-size: 16px;
	font-weight: 300;
	text-align: left;
	white-space: nowrap;
  	overflow: hidden;
}
.newBank {
	text-decoration: none;
	color: white;
	padding: 6px 12px;
	font-size: 14px;
	border-radius: 5px;
	background: #ee7419;
}
.newBank::before{ 
	content:"+";
}

.button{
	padding: 4px;
	width: 35px;
	margin: 0 5px;
	float: left;
	text-align: center;
	border-radius: 5px;
	color: white;
	font-size: 12px;
}
.default {
	background: #f7f7f7;
	color: #495060;
}
.warning {
	color: #fff;
	background-color: #f90;
}
.error {
  color: #fff;
  background-color: #ed3f14;
}
.success {
	background: #19be6b;
	color: #fff;
}

